.notification-trailer-fail-overlay {
  display: flex;
  visibility: hidden;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  overflow-y: scroll;
}

.notification-trailer-fail {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  width: 280px;
  height: 289px;
  background: var(--grey-text-color);
  box-shadow: var(--modal-box-shadow);
  border-radius: 16px;
  z-index: 10;
}

@include tablet {
  .notification-trailer-fail {
    width: 609px;
    height: 400px;
    z-index: 10;
  }
}

@include desktop-notification {
  .notification-trailer-fail {
    width: 700px;
    height: 400px;
    border-radius: 20px;
    z-index: 10;
  }
}
.notification-trailer-fail-text {
  width: 214px;
  height: 65px;
  margin-top: 3px;
  margin-left: 33px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: var(--second-text-color);
}

@include tablet {
  .notification-trailer-fail-text {
    width: 321px;
    height: 92px;
    margin-top: 84px;
    margin-left: 44px;
    font-size: 24px;
    line-height: 28px;
  }
}

@include desktop-notification {
  .notification-trailer-fail-text {
    margin-top: 98px;
    margin-left: 50px;
    width: 321px;
    height: 100px;
  }
}

.notification-trailer-fail-label {
  margin-top: 24px;
  margin-left: 61px;
}

@include tablet {
  .notification-trailer-fail-label {
    margin-left: 303px;
    margin-top: -100px;
  }
}

@include desktop-notification {
  .notification-trailer-fail-label {
    margin-left: 337px;
    margin-top: -173px;
  }
}
.notification-trailer-fail-btn-close {
  width: 16px;
  height: 16px;
  padding: 0;
  border: none;
  margin-top: 20px;
  margin-left: 244px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  cursor: pointer;
}

@include tablet {
  .notification-trailer-fail-btn-close {
    width: 24px;
    height: 24px;
    margin-top: 24px;
    margin-left: 561px;
  }
}

@include desktop-notification {
  .notification-trailer-fail-btn-close {
    margin-top: 28px;
    margin-left: 648px;
  }
}

.notification-trailer-fail-btn-close__icon {
  width: 16px;
  height: 16px;
}
